const mod=require('./index');const indexExports=mod.default||mod;describe('Index Exports',()=>{test('should include all expected utility categories',()=>{expect(indexExports.formatDateTime).toBeDefined();expect(indexExports.formatDuration).toBeDefined();expect(indexExports.addDays).toBeDefined();expect(indexExports.formatDate).toBeDefined();expect(indexExports.formatDateWithPrefix).toBeDefined();expect(indexExports.ensureProtocol).toBeDefined();expect(indexExports.normalizeUrlOrigin).toBeDefined();expect(indexExports.stripProtocol).toBeDefined();expect(indexExports.parseUrlParts).toBeDefined();expect(indexExports.generateExecutionId).toBeDefined();expect(indexExports.sanitizeString).toBeDefined();expect(indexExports.formatFileSize).toBeDefined();expect(indexExports.logger).toBeDefined()});test('should have correct function signatures for datetime utilities',()=>{expect(typeof indexExports.formatDate).toBe('function');expect(typeof indexExports.formatDateWithPrefix).toBe('function')});test('should have correct function signatures for string utilities',()=>{expect(typeof indexExports.sanitizeString).toBe('function')});test('should have correct function signatures for file utilities',()=>{expect(typeof indexExports.formatFileSize).toBe('function')});test('should have correct function signatures for ID generation utilities',()=>{expect(typeof indexExports.generateExecutionId).toBe('function')})});
